import "core-js/modules/es.array.push.js";
import { reactive, ref, toRefs } from '@vue/reactivity';
import { getmode } from "../assets/api/home";
import { useRoute, useRouter } from 'vue-router';
export default {
  setup() {
    let route = useRoute();
    let router = useRouter();
    let name = route.query.title;
    console.log(name);
    const loading = ref(false);
    const finished = ref(false);
    const onLoad = () => {
      getmode("", "2").then(res => {
        res.data.results.list.forEach(item => {
          ac.dataList.push(item);
        });
      });

      // 加载状态结束
      loading.value = false;

      // 数据全部加载完成
      if (ac.dataList.length >= 10) {
        finished.value = true;
      }
    };
    let ac = reactive({
      // 全部
      dataList: [],
      //   系统课
      class1: [],
      //   公开课课
      class2: []
    });
    let getmodeFun = () => {
      // 全部
      getmode("", "1").then(res => {
        console.log(res);
        ac.dataList = res.data.results.list;
        console.log(ac.dataList);
      });
      // 系统课
      getmode("1", "1").then(res => {
        console.log(res);
        ac.class1 = res.data.results.list;
        console.log(ac.class1);
      });
      // 公开课
      getmode("2").then(res => {
        console.log(res);
        ac.class2 = res.data.results.list;
        console.log(ac.class2);
      });
    };
    getmodeFun();
    const active = ref(0);
    let goshop = key => {
      router.push({
        path: "/netshop",
        query: {
          key: key
        }
      });
    };
    return {
      ...toRefs(ac),
      active,
      onLoad,
      loading,
      finished,
      name,
      goshop
    };
  }
};